                        &.five{
                            background: #39EFA11A;
                        }
                        &.six{
                            background: #F040371A;
                        }
                    }
                    .ms-sb--inner{
                        .ms-sb--title{
                            font-weight: 800;
                            span{
                                color: #262626;
                                font-size: 20px;
                            }
                        }
                        .ms-sb--text{
                            color: #262626;
                        }
                    }
                }
            }
            .services-bottom{
                margin-top: 70px;
                .sub-title{
                    color: #262626;
                    font-size: 16px;
                    text-transform: uppercase;
                    margin-bottom: 20px;
                }
                .heading-title{
                    color: #262626;
                    font-family: "Manrope", Sans-serif;
                    font-size: 60px;
                    font-weight: 900;
                    line-height: 64px;
                    margin-bottom: 30px;
                    @media(max-width:1024px){
                        text-align: center;
                        font-size: 2.5em;
                        line-height: 1.4em;
                    }
                    @media(max-width:767px){
                        font-size: 2em;
                        line-height: 1.4em;
                    }
                    @media(max-width:400px){
                        br{
                            display: none;
                        }
                    }
                }
                .project-item{
                    .portfolio_wrap{
                        .portfolio-feed{
                            padding: 0;
                            background: none;
                            .grid-item-p{
                                padding-right: calc(3rem/ 2);
                                padding-left: calc(3rem/ 2);
                                margin-top: 3rem;
                            }
                        }
                    }
                }
            }
        }
    }
}

